import React from 'react';
import './styles.css';


function UserTippy() {
  return (
    <div id='user-tippy'>
        <div className='user-tippyWidget'>
          <div>Add an existing account</div>
        </div>
        <div className='user-tippyWidget'
        style={{
          borderBottomLeftRadius: '15px',
          borderBottomRightRadius: '15px'
        }}
        ><div>Log out @lasertoch</div></div>
    </div>
  )
}

export default UserTippy;