import React from 'react';
import './styles.css';

function SideNavBar() {
  return (
    <div id='sideNavBar'>
        <div id='sideNavBarTop'>
            <div id='sideNavBarTopText'>Account Info</div>
            <div id='sideNavBarTopImageContainer'>
                <img
                    src='https://i.postimg.cc/L4Y528fg/wrong-black.png'
                    alt='closeIcon'
                />
            </div>
        </div>
    </div>
  )
}

export default SideNavBar;